import React, { useState } from 'react';
import axios from 'axios'

import NavBar from '../private/components/NavBar'
import Search from '../private/components/Search'
import Results from '../private/components/Results'
import Popup from '../private/components/Popup'
import './Database.css';


export default function Database() {
    const [state, setState] = useState({
        s: "",
        results: [],
        selected: {}
    });

    const apiurl = "http://www.omdbapi.com/?apikey=e783f1ec";
    // const apiurl = "http://www.omdbapi.com/?apikey=dfe6d885";

    const search = (e) => {
        if (e.key === "Enter") {
            axios(apiurl + "&s=" + state.s).then(({ data }) => {
                let results = data.Search;

                setState(prevState => {
                    return { ...prevState, results: results }
                })
            });
        }
    }

    const handleInput = (e) => {
        let s = e.target.value;

        setState(prevState => {
            return { ...prevState, s: s }
        });
    }

    const openPopup = id => {
        axios(apiurl + "&i=" + id).then(({ data }) => {
            let result = data;

            console.log(result);

            setState(prevState => {
                return { ...prevState, selected: result }
            });
        });
    }

    const closePopup = () => {
        setState(prevState => {
            return { ...prevState, selected: {} }
        });
    }

    return (
        <div>
            <NavBar />
            <h1>Movie Database</h1>
            <Search handleInput={handleInput} search={search} />

            <Results results={state.results} openPopup={openPopup} />

            {(typeof state.selected.Title != "undefined") ? <Popup selected={state.selected} closePopup={closePopup} /> : false}

        </div>
    )
}